
@charset "UTF-8";
/*
 * 基础色
 */
.cat-toast {
  position: fixed;
  top: 50px;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 8px 15px;
  color: #808080;
  background-color: #fff;
  box-shadow: 1px 2px 1px #d1d1d1;
}
.cat-toast-primary {
    color: #fff;
    background-color: #3da8f5;
}
.cat-toast-error {
    color: #fff;
    background-color: #d71345;
}
.cat-toast-warning {
    color: #fff;
    background-color: #ffaf38;
}
.cat-toast-success {
    color: #fff;
    background-color: #00B281;
}



